.service-page {
    max-width: 1040px;
    margin: 0 auto;

    color: #5b5b5b;
    font-family: $roboto;

    .wrapper {
        margin-bottom: 40px;
    }

    .title {
        width: 375px;
        margin: 0;
        margin-bottom: 20px;
        padding: 3px 15px;

        background-color: $themecolor;

        color: $ligthtext;
        font-size: 18px;
        font-weight: 600;

        .counter {
            margin-right: 15px;
        }
    }

    p {
        padding-left: 15px;
        margin: 0;
        margin-bottom: 10px;
        font-size: 18px;
        font-weight: 600;
    }

    ul {
        padding-left: 45px;
        margin: 0;
        margin-bottom: 10px;
        font-size: 18px;
        font-weight: 600;
    }

}
